.productaccordian{
    margin: 50px 0;
}
.productAccordianInput {
    display: none;
}

.productAccordianLabel {
    display: block;    
    padding: 18px 22px;
    margin: 0 0 1px 0;
    cursor: pointer;
    background: transparent;
    border-radius: 3px;
    color: #111;
    transition: ease .5s;
    border-bottom: 1px solid rgb(139, 138, 138);
    height: 50px;
    /* font-size: medium !important; */


}
.productAccordianLabel span{
    float: right;
    color: lightslategrey;
}

.productAccordianLabel:hover {
    background: #fbfcfc;
}

.accordianContent {
    transition: .3s ease-in-out;
    background: #ffffff;
    padding: 10px 25px;
    margin: 0 0 1px 0;
    border-radius: 3px;
    height: auto;
}
.accordianContent p{
    margin: 10px auto;
    color: rgb(0, 0, 0);
}
.accordianContent p span{
    text-decoration: none;
    color: rgb(109, 105, 105);
}
.productAccordianInput + .productAccordianLabel + .accordianContent {
    display: none;
}

.productAccordianInput:checked + .productAccordianLabel + .accordianContent {
    display: block;
}

