/* code for arrow, page-up*/
.termCondition #return-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    /* I can add here some background-color */
    border: 1px solid white;

    width: 50px;
    height: 50px;
    display: block;
    text-decoration: none;
    border-radius: 35px;
    display: none;
    transition: all 0.3s ease;
}
.termCondition #return-to-top i { /* inside arrow */
    color: #fff;
    margin: 0;
    position: relative;
    left: 16px;
    top: 13px;
    font-size: 19px;
    transition: all 0.3s ease;
}
.termCondition #return-to-top:hover {
    background:  white /*rgba(0, 0, 0, 0.9)*/;
}
.termCondition #return-to-top:hover i { /* inside arrow */
    color: rgb(95, 95, 95); /* grey */
    top: 5px;
}

/* end of code for arrow, page-up*/

