.quotationwrapper{
    display: inline-block!important;
    margin-top: 90px;
}
.topics_m{
  display: none;
}
  .quotationwrapper .button,.quotationwrapper div{
    display: inline-block!important;
    height: 50px;
    width: 160px;
    margin: 0 5px;
    overflow: visible;
    background: #fff;
    border-radius: 0px;
    cursor: pointer;
    box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
    transition: all 0.3s ease-out;
  }
  .quotationwrapper div{
    box-shadow: none;
    width: 150px;
    font-size: 14px;
    font-weight: 600;
  }
  .quotationHeading{
      background-color: transparent!important;
  }
  .quotationwrapper div h2{
    text-decoration: underline;
  }
  .quotationwrapper .button .icon{
    display: inline-block;
    height: 50px;
    width: 160px;
    text-align: center;
    border-radius: 0px;
    box-sizing: border-box;
background-color: white;
color: black;
    transition: all 0.3s ease-out;
    font-size: 12px;
    line-height: 30px;
  }

  .quotationwrapper .button:hover .icon{
    background: #383838;
    color: rgb(255, 255, 255);
  }

  @media (max-width:920px) {
